* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  scroll-snap-type: y mandatory;
  overflow-y: scroll;
  height: 100vh;
  background: #e7e7e7;
  mix-blend-mode: normal;
}

.container::-webkit-scrollbar {
  display: none; /* for Chrome, Safari, and Opera */
}
